<template>
    <div class="lyrics-container">
      <!-- 播放音乐按钮 -->
      <button @click="toggleMusic" class="play-music-button">
        {{ isPlaying ? '暂停音乐' : '播放音乐' }}
      </button>
      <div class="lyrics-content">
        <div class="row" v-for="(line, index) in lyrics" :key="index">
          <img
            class="notation-image"
            :src="line.image"
            :style="{ marginLeft: line.imageOffset + 'px' }"
          >
          <p class="text">
            <span
              v-for="(charObj, charIndex) in line.text"
              :key="charIndex"
              class="char"
              :style="{ transform: `translateX(${charObj.offsetX}px)` }"
            >
              {{ charObj.char }}
            </span>
          </p>
          <!-- 乐器放置区域 -->
          <div class="instrument-boxes">
            <div
              v-for="(charObj, charIndex) in line.text"
              :key="charIndex"
              class="instrument-box"
              :style="{
                left: charObj.instrumentX + 'px',
                top: charObj.instrumentY + 'px'
              }"
              @click="placeInstrument(index, charIndex)"
            >
              <img v-if="line.instruments && line.instruments[charIndex]" :src="line.instruments[charIndex]" alt="Instrument" class="instrument-image">
            </div>
          </div>
        </div>
      </div>
      <!-- 右侧导航栏 -->
      <div :class="{ 'nav-open': isNavOpen, 'nav-closed':!isNavOpen }" class="instrument-nav">
        <button @click="toggleNav" class="nav-toggle">
          {{ isNavOpen ? '关闭导航' : '打开导航' }}
        </button>
        <div class="instrument-list">
          <img
            v-for="(instrument, instrumentIndex) in instruments"
            :key="instrumentIndex"
            :src="instrument.image"
            alt="Instrument"
            @click="selectedInstrument = instrument"
            class="instrument-item"
          >
        </div>
      </div>
    </div>
  </template>

