          ],
          instruments: []
        }
      ],
      instruments: [
        { image: 'guzhang.jpg' },
        { image: 'jiao.png' }
      ],
      selectedInstrument: null,
      isNavOpen: false,
      isPlaying: false,
      audio: new Audio('alyy.mp3'),
      currentTime: 0
    }
  },
  methods: {
    toggleNav () {
      this.isNavOpen = !this.isNavOpen
    },
    placeInstrument (lineIndex, charIndex) {
      if (this.selectedInstrument) {
        if (!this.lyrics[lineIndex].instruments) {
          this.lyrics[lineIndex].instruments = []
        }
        this.lyrics[lineIndex].instruments[charIndex] = this.selectedInstrument.image
        this.lyrics[lineIndex].instrumentAudios = this.lyrics[lineIndex].instrumentAudios || []
        this.lyrics[lineIndex].instrumentAudios[charIndex] = this.selectedInstrument.audio
      }
    },
    toggleMusic () {
      if (this.isPlaying) {
        this.audio.pause()
        clearInterval(this.timer)
      } else {
        this.audio.play()
        this.timer = setInterval(() => {
          this.currentTime = this.audio.currentTime
          this.checkInstrumentPlay()
        }, 100)
      }
      this.isPlaying = !this.isPlaying
    },
    checkInstrumentPlay () {
      this.lyrics.forEach((line, lineIndex) => {
        line.text.forEach((charObj, charIndex) => {
          if (
            line.instruments &&
              line.instruments[charIndex] &&
              line.instrumentAudios &&
              line.instrumentAudios[charIndex] &&
              this.currentTime >= charObj.playTime &&
              !charObj.hasPlayed
          ) {
            line.instrumentAudios[charIndex].play()
            charObj.hasPlayed = true
          }
        })
      })
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  }
}
