    },
    changeLyrics (newLyrics) {
      this.lyrics.forEach((line, lineIndex) => {
        if (newLyrics[lineIndex]) {
          const newText = newLyrics[lineIndex].text.split('').map((char, charIndex) => {
            return {
              char,
              offsetX: line.text[charIndex] ? line.text[charIndex].offsetX : 0,
              playTime: line.text[charIndex] ? line.text[charIndex].playTime : 0,
              instrumentX: line.text[charIndex] ? line.text[charIndex].instrumentX : 0,
              instrumentY: line.text[charIndex] ? line.text[charIndex].instrumentY : 0
            }
          })
          line.text = newText
        }
      })
    },
    toggleMusic () {
      if (this.isPlaying) {
        this.audio.pause()
        clearInterval(this.timer)
      } else {
        this.audio.play()
        this.timer = setInterval(() => {
          this.currentTime = this.audio.currentTime
          this.checkInstrumentPlay()
        }, 100)
      }
      this.isPlaying = !this.isPlaying
    },
    checkInstrumentPlay () {
      this.lyrics.forEach((line, lineIndex) => {
        line.text.forEach((charObj, charIndex) => {
          if (
            line.instruments &&
                        line.instruments[charIndex] &&
                        line.instrumentAudios &&
                        line.instrumentAudios[charIndex] &&
                        this.currentTime >= charObj.playTime &&
                        !charObj.hasPlayed
          ) {
            line.instrumentAudios[charIndex].play()
            charObj.hasPlayed = true
          }
        })
      })
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  }
}
