<template>
    <div id="app">

      <!-- 输入对话框 -->
      <div v-if="!showImage" class="dialog-container">
        <h1>AI趣味图片展示</h1>
        <input
          type="text"
          v-model="inputText"
          placeholder="输入描述..."
          class="input-field"
        />
        <button @click="generateImage" class="generate-button">点击显示图片</button>
      </div>

      <!-- 图片展示区域 -->
      <div v-if="showImage" class="image-container">
        <img src="zp.jpg" alt="Generated Image" class="generated-image">
        <button @click="showImage = false" class="return-button">返回重新生成</button>
      </div>
    </div>
  </template>

<script>
export default {
  data () {
    return {
      inputText: '',
      showImage: false,
      isAudioPlaying: false,
      audioElement: null
    }
  },
  mounted () {
    // 初始化音频
    this.audioElement = new Audio('xc1.mp3') // 替换你的音频路径
    this.audioElement.loop = true // 开启循环播放
  },
  methods: {
    generateImage () {
      // 这里可以添加图片生成逻辑
      this.showImage = true
    },
    toggleAudio () {
      this.isAudioPlaying = !this.isAudioPlaying
      if (this.isAudioPlaying) {
        this.audioElement.play()
      } else {
        this.audioElement.pause()
      }
    }
  },
  beforeDestroy () {
    // 组件销毁时停止音频
    if (this.audioElement) {
      this.audioElement.pause()
      this.audioElement = null
    }
  }
}
</script>

  <style scoped>
  .audio-control {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.9);
    border: 2px solid #007bff;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }

  .audio-control:hover {
    transform: scale(1.1);
    background: #007bff;
    color: white;
  }

  .audio-control.is-playing {
    background: #007bff;
    color: white;
  }

  .image-container {
    max-width: 200px;
    margin: 20px auto;
    text-align: center;
    animation: fadeIn 0.5s ease;
  }

  .generated-image {
    width: 100%;
    max-width: 600px;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0,0,0,0.2);
  }

  .return-button {
    display: block;
    margin: 20px auto;
    padding: 10px 25px;
    background: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: transform 0.3s ease;
  }

  .return-button:hover {
    transform: scale(1.05);
    background: #45a049;
  }

  /* 对话框进入/离开动画 */
  .dialog-container {
    transition: all 0.5s ease;
  }

  @keyframes fadeIn {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
  }

  body {
    background-color: #f8f9fa; /* 背景色 */
    font-family: Arial, sans-serif; /* 字体样式 */
  }

  #app {
    max-width: 600px;
    margin: 50px auto; /* 增加了上边距 */
    padding: 20px; /* 增加了内边距 */
    background-color: #fff; /* 输入框背景色 */
    border-radius: 10px; /* 边框圆角 */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* 阴影效果 */
    text-align: center;
  }

  h1 {
    color: #333; /* 标题颜色 */
    margin-bottom: 20px; /* 标题下边距 */
  }

  .input-field {
    width: 80%;
    padding: 15px; /* 输入框内边距 */
    margin-bottom: 20px; /* 下边距 */
    border: 1px solid #ced4da; /* 边框样式 */
    border-radius: 5px; /* 边框圆角 */
    transition: border-color 0.3s; /* 过渡效果 */
  }

  .input-field:focus {
    border-color: #007bff; /* 聚焦时边框颜色 */
    outline: none; /* 去掉默认边框 */
  }

  .generate-button {
    padding: 10px 20px; /* 按钮内边距 */
    background-color: #007bff; /* 按钮背景色 */
    color: white; /* 按钮文字颜色 */
    border: none; /* 去掉边框 */
    border-radius: 5px; /* 圆角边框 */
    cursor: pointer; /* 鼠标悬停指针 */
    transition: background-color 0.3s, transform 0.3s; /* 过渡效果 */
  }

  .generate-button:hover {
    background-color: #0056b3; /* 悬停时按钮背景色 */
    transform: scale(1.05); /* 鼠标悬停时放大 */
  }
  /* 添加路由过渡动画 */
  .magic-portal-enter-active,
  .magic-portal-leave-active {
    transition: all 1s ease;
    position: absolute;
    width: 100%;
  }

  .magic-portal-enter-from {
    transform: translateX(100%) rotate(360deg);
    opacity: 0;
  }

  .magic-portal-leave-to {
    transform: translateX(-100%) rotate(-360deg);
    opacity: 0;
  }

  /* 调整原有按钮样式 */
  .magic-jump-button {
    /* 保持原有样式 */
    /* 添加以下新属性 */
    transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
  }

  .magic-jump-button:hover {
    transform:
      translateY(-5px)
      rotate(2deg)
      scale(1.05);
    filter: hue-rotate(45deg);
  }

  /* 添加传送门打开效果 */
  .portal-effects::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(
      circle at var(--x) var(--y),
      rgba(255,255,255,0.3) 10%,
      transparent 30%
    );
    pointer-events: none;
  }
  </style>
